 	export default {
 		data() {
			return {
				cashMoney: '',
				hasMoney: 869.5
			}
		},
		computed: {
			showTips1() {
				if(Number(this.cashMoney) > Number(this.hasMoney)) {
					return false
				}else{
					return true
				}
			}
		},
		methods: {
			onChange(v) {
				this.cashMoney = v.detail
			},
			cashTotal() {
				this.cashMoney = this.hasMoney
			}
		}
 	}
 